*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  /* Font varient */
  font-variant-ligatures: none;
  -webkit-font-variant-ligatures: none;
  /* Smoothing */
  text-rendering: optimizelegibility;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}

body {
  text-rendering: optimizelegibility;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  overflow-x: hidden;
  position: relative;
}

input,
select,
textarea,
button {
  -webkit-tap-highlight-color: transparent;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

body {
  background-color: black;
  color: white;
  text-align: center;
}

a {
  background-color: black;
  border: 1px solid white;
  color: white;
  padding: 10px 30px;
}
a:hover {
  background-color: white;
  color: black;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.help-modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.2);
  position: fixed;
  top: 0;
  left: 0;
}
.help-modal__content {
  background-color: black;
  color: white;
  width: 100%;
  max-width: 600px;
  max-height: 100vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  overflow: auto;
  border: 5px solid #fff;
  overflow: auto;
}
.help-modal__content p {
  margin-bottom: 10px;
}
.help-modal__content p:last-of-type {
  margin-bottom: 50px;
}
.help-modal__content h2 {
  margin-bottom: 20px;
}
.help-modal.hidden {
  display: none;
}

.maze {
  margin: 30px auto;
  max-width: 600px;
  height: 600px;
  outline: 2px solid white;
  position: relative;
}
.maze__announcer {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: black;
  border-radius: 9px;
  padding: 5px;
}
.maze__announcer--encounter {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: black;
  border-radius: 9px;
  padding: 5px;
}
.maze__score {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px;
}
.maze__rooms {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
}
.maze__encounter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.maze__menu-background {
  position: absolute;
  z-index: -1;
}
.maze__menu-background--1 {
  top: 20%;
  left: 15%;
}
@media screen and (max-width: 768px) {
  .maze__menu-background--1 {
    top: 10%;
    left: 10%;
  }
}
.maze__menu-background--2 {
  top: 20%;
  right: 15%;
}
@media screen and (max-width: 768px) {
  .maze__menu-background--2 {
    top: 10%;
    right: 10%;
  }
}
.maze__menu-background--3 {
  bottom: 20%;
  left: 15%;
}
@media screen and (max-width: 768px) {
  .maze__menu-background--3 {
    bottom: 10%;
    left: 10%;
  }
}
.maze__menu-background--4 {
  bottom: 20%;
  right: 15%;
}
@media screen and (max-width: 768px) {
  .maze__menu-background--4 {
    bottom: 10%;
    right: 10%;
  }
}
.maze__menu-background.dancing-troll {
  animation: dance 1s ease-in-out infinite;
}
@keyframes dance {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.maze__level-select {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 50%;
  bottom: 20%;
  transform: translate(-50%, -50%);
}
.maze__level {
  background: black;
  color: white;
  padding: 5px;
  margin-top: 10px;
}
.maze .flag {
  position: absolute;
  top: 30%;
  left: 30%;
}

.player {
  width: 30px;
  height: 30px;
  background-color: blue;
  position: absolute;
}

.game {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}
.game * {
  flex-grow: 1;
}
.game__input {
  height: 35px;
  width: 100%;
  max-width: 400px;
  text-align: center;
  font-size: 24px;
  position: relative;
}
.enemy.defeated {
  animation: defeated 1s ease-in forwards;
}
@keyframes defeated {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(0);
  }
}

.reward.defeated {
  animation: defeated 1s ease-in forwards;
}
@keyframes defeated {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

.doors {
  position: absolute;
  background-color: black;
}
.doors::before, .doors::after {
  content: "";
  position: absolute;
  transition: all 0.3s ease-in-out;
  background-color: white;
}
.doors--north, .doors--south {
  width: 80px;
  height: 4px;
}
.doors--north::before, .doors--north::after, .doors--south::before, .doors--south::after {
  height: 40px;
  width: 2px;
}
.doors--north {
  top: -2px;
  left: 50%;
  transform: translateX(-50%);
}
.doors--north::before {
  top: 1px;
  left: calc(50% - 41px);
  transform-origin: top center;
}
.doors--north::after {
  top: 1px;
  left: calc(50% + 40px);
  transform-origin: top center;
}
.doors--south {
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%);
}
.doors--south::before {
  bottom: 1px;
  left: calc(50% - 41px);
  transform-origin: bottom center;
}
.doors--south::after {
  bottom: 1px;
  left: calc(50% + 40px);
  transform-origin: bottom center;
}
.doors--east, .doors--west {
  height: 80px;
  width: 4px;
}
.doors--east::before, .doors--east::after, .doors--west::before, .doors--west::after {
  width: 40px;
  height: 2px;
}
.doors--east {
  top: 50%;
  right: -2px;
  transform: translateY(-50%);
}
.doors--east::before {
  top: calc(50% - 41px);
  right: 1px;
  transform-origin: center right;
}
.doors--east::after {
  top: calc(50% + 40px);
  right: 1px;
  transform-origin: center right;
}
.doors--west {
  top: 50%;
  left: -2px;
  transform: translateY(-50%);
}
.doors--west::before {
  top: calc(50% - 41px);
  left: 1px;
  transform-origin: center left;
}
.doors--west::after {
  top: calc(50% + 40px);
  left: 1px;
  transform-origin: center left;
}
.doors.closed.doors--north::before {
  transform: rotate(-90deg);
}
.doors.closed.doors--north::after {
  transform: rotate(90deg);
}
.doors.closed.doors--south::before {
  transform: rotate(90deg);
}
.doors.closed.doors--south::after {
  transform: rotate(-90deg);
}
.doors.closed.doors--east::before {
  transform: rotate(-90deg);
}
.doors.closed.doors--east::after {
  transform: rotate(90deg);
}
.doors.closed.doors--west::before {
  transform: rotate(90deg);
}
.doors.closed.doors--west::after {
  transform: rotate(-90deg);
}

.hero {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition-property: top, left;
  transition-duration: 1s;
  transition-timing-function: ease-in-out;
}
.hero.arriveFromNorth {
  animation: arriveFromNorth 1s ease-in-out forwards;
}
.hero.arriveFromSouth {
  animation: arriveFromSouth 1s ease-in-out forwards;
}
.hero.arriveFromEast {
  animation: arriveFromEast 1s ease-in-out forwards;
}
.hero.arriveFromWest {
  animation: arriveFromWest 1s ease-in-out forwards;
}
.hero.leaveToNorth {
  animation: leaveToNorth 1s ease-in-out forwards;
}
.hero.leaveToSouth {
  animation: leaveToSouth 1s ease-in-out forwards;
}
.hero.leaveToEast {
  animation: leaveToEast 1s ease-in-out forwards;
}
.hero.leaveToWest {
  animation: leaveToWest 1s ease-in-out forwards;
}
.hero.fromNorthEncounter {
  animation: arriveFromNorthEncounter 1s ease-in-out forwards;
}
.hero.fromSouthEncounter {
  animation: arriveFromSouthEncounter 1s ease-in-out forwards;
}
.hero.fromEastEncounter {
  animation: arriveFromEastEncounter 1s ease-in-out forwards;
}
.hero.fromWestEncounter {
  animation: arriveFromWestEncounter 1s ease-in-out forwards;
}
.hero.toCenterFromNorth {
  animation: toCenterFromNorth 1s ease-in-out forwards;
}
.hero.toCenterFromSouth {
  animation: toCenterFromSouth 1s ease-in-out forwards;
}
.hero.toCenterFromEast {
  animation: toCenterFromEast 1s ease-in-out forwards;
}
.hero.toCenterFromWest {
  animation: toCenterFromWest 1s ease-in-out forwards;
}

@keyframes arriveFromNorth {
  0% {
    top: 0%;
    left: 50%;
  }
  100% {
    top: 50%;
    left: 50%;
  }
}
@keyframes arriveFromSouth {
  0% {
    top: 100%;
    left: 50%;
  }
  100% {
    top: 50%;
    left: 50%;
  }
}
@keyframes arriveFromEast {
  0% {
    left: 100%;
    top: 50%;
  }
  100% {
    left: 50%;
    top: 50%;
  }
}
@keyframes arriveFromWest {
  0% {
    left: 0%;
    top: 50%;
  }
  100% {
    left: 50%;
    top: 50%;
  }
}
@keyframes leaveToNorth {
  from {
    top: 50%;
    left: 50%;
  }
  to {
    top: -10%;
    left: 50%;
  }
}
@keyframes leaveToSouth {
  from {
    top: 50%;
    left: 50%;
  }
  to {
    top: 110%;
    left: 50%;
  }
}
@keyframes leaveToEast {
  from {
    top: 50%;
    left: 50%;
  }
  to {
    top: 50%;
    left: 110%;
  }
}
@keyframes leaveToWest {
  from {
    top: 50%;
    left: 50%;
  }
  to {
    top: 50%;
    left: -10%;
  }
}
@keyframes arriveFromNorthEncounter {
  0% {
    top: -10%;
    left: 50%;
  }
  100% {
    top: 35%;
    left: 50%;
  }
}
@keyframes arriveFromSouthEncounter {
  0% {
    top: 110%;
    left: 50%;
  }
  100% {
    top: 65%;
    left: 50%;
  }
}
@keyframes arriveFromEastEncounter {
  0% {
    top: 50%;
    left: 110%;
  }
  100% {
    top: 50%;
    left: 80%;
  }
}
@keyframes arriveFromWestEncounter {
  0% {
    top: 50%;
    left: -10%;
  }
  100% {
    top: 50%;
    left: 20%;
  }
}
@keyframes toCenterFromNorth {
  0% {
    top: 35%;
    left: 50%;
  }
  100% {
    top: 50%;
    left: 50%;
  }
}
@keyframes toCenterFromSouth {
  0% {
    top: 65%;
    left: 50%;
  }
  100% {
    top: 50%;
    left: 50%;
  }
}
@keyframes toCenterFromEast {
  0% {
    top: 50%;
    left: 80%;
  }
  100% {
    top: 50%;
    left: 50%;
  }
}
@keyframes toCenterFromWest {
  0% {
    top: 50%;
    left: 20%;
  }
  100% {
    top: 50%;
    left: 50%;
  }
}